const desc = `
  # Modal
  
`;

const base = `
  \`\`\`jsx
  import React, { Component } from 'react';
  import ReactDOM from 'react-dom';
  import { Modal } from 'react-stone';

  class App extends Component {
    constructor(props) {
      super(props);
      this.state = {
      };
    }
    openModal = () => {
      const myModal = Modal.open({
        "size": 'sm',
        "title": '',
        "template": this.getContentComponent(),
        "onCancel": this.closeModal,
        "onOk": this.okModal
      });
    }
    getContentComponent = () => {
      return (
        <p></p>
      )
    }
    render(){
      return (
        <div>
          <Button onClick={this.openModal}>Open modal by JavaScript</Button>
        </div>
      )
    }
  }

  ReactDOM.render(<App />, document.getElementById('root'));
  \`\`\`
`;

const template = `
  \`\`\`jsx
  import React, { Component } from 'react';
  import ReactDOM from 'react-dom';
  import { Modal } from 'react-stone';

  class App extends Component {
    constructor(props) {
      super(props);
      this.state = {
        isShowModal: false,
      };
    }
    handleModalVisible = () => {
      this.setState({
        isShowModal: true
      })
    }
    render(){
      return (
        <div>
          <Modal
            title=''
            visible={this.state.isShowModal}>
            Hello Modal
          </Modal>
          <Button onClick={this.handleModalVisible}>Open modal by templeate</Button>
        </div>
      )
    }
  }

  ReactDOM.render(<App />, document.getElementById('root'));
  \`\`\`
`;

const api = `
  ## 
     |         |     
  ----    |  ----    | ----    |
  open |  | modal
  close |  | modal


  ## Options
     |       |          |     
  ----    | ----    | ----    | ----    |
  mask  | Boolean  | true |  
  closeable  | Boolean  | true  |  
  footer | Boolean | true | 
  cancelBtn | Boolean | true | 
  okText | String | '' | 
  cancelText | String | '' | 
  template | JSX |  | Component 
  title | StringReactnode |  | 
  size | String | 'sm' | 'sm','','lg'false   'sm'  'sm' {width:'400px',height:'300px'} size false {width:'600px',height:'400px'}  'lg' {width:'900px',height:'600px'}

  ## Events
     |        |        |
  ----    | ----      | ----        |
  onCancel  |  | 
  onOk  |  | 
`;

export {
  desc,
  base,
  template,
  api,
};



