const desc = `
  # Checkbox
  
`;

const base = `
  \`\`\`jsx
  import React, { Component } from 'react';
  import ReactDOM from 'react-dom';
  import { CheckboxGroup } from 'react-stone';

  class App extends Component {
    constructor(props) {
      super(props)
      this.state = {
        options: [
          { label: 'Apple', value: 'Apple' },
          { label: 'Pear', value: 'Pear' },
          { label: 'Orange', value: 'Orange' },
        ],
        checkedBox: [
          'Pear'
        ]
      }
    }
    checkedBoxChange = (checkedList, targetValue) => {
      console.log(checkedList);
      console.log(targetValue);
    }
    render(){
      return (
        <CheckboxGroup
          options={this.state.options}
          onChange={this.checkedBoxChange}
          defaultValue={['Apple']}
          value={this.state.checkedBox}/>
      )
    }
  }

  ReactDOM.render(<App />, document.getElementById('root'));
  \`\`\`
`;

const defaultvalue = `
  \`\`\`jsx
  import React, { Component } from 'react';
  import ReactDOM from 'react-dom';
  import { CheckboxGroup } from 'react-stone';

  class App extends Component {
    constructor(props) {
      super(props)
      this.state = {
        options: [
          { label: '', value: '' },
          { label: '', value: '' },
          { label: '', value: '' },
        ],
        checkedBox: [
          'Pear'
        ]
      }
    }
    checkedBoxChange = (checkedList, targetValue) => {
      console.log(checkedList);
      console.log(targetValue);
    }
    render(){
      return (
        <CheckboxGroup
          options={this.state.options}
          onChange={this.checkedBoxChange}
          defaultValue={['']}
          value={this.state.checkedBox}/>
      )
    }
  }

  ReactDOM.render(<App />, document.getElementById('root'));
  \`\`\`
`;

const disabled = `
  \`\`\`jsx
  import React, { Component } from 'react';
  import ReactDOM from 'react-dom';
  import { CheckboxGroup } from 'react-stone';

  class App extends Component {
    constructor(props) {
      super(props)
      this.state = {
        options: [
          { label: '', value: 'Nike', disabled: true },
          { label: '', value: 'Adidas', disabled: true },
        ],
        checkedBox: [
          'Pear'
        ]
      }
    }
    checkedBoxChange = (checkedList, targetValue) => {
      console.log(checkedList);
      console.log(targetValue);
    }
    render(){
      return (
        <CheckboxGroup
          options={this.state.options}
          onChange={this.checkedBoxChange}
          value={this.state.checkedBox}/>
      )
    }
  }

  ReactDOM.render(<App />, document.getElementById('root'));
  \`\`\`
`;

const api = `
  ## Options

     |       |          |     
  ----    | ----    | ----    | ----    |
  options  | Array  | '' |  
  defaultValue | Array | '' | 
  value | Array | '' | 
  disabled | Bool | false | options

  ## Events
     |        |        |
  ----    | ----      | ----        |
  onChange | CheckboxGroup | (checkedList, targetValue), 
`;

export {
  desc,
  base,
  defaultvalue,
  disabled,
  api,
};



