const desc = `
  # Toast 

  

  ## 

  

  

  Toast
`;

const base = `
  \`\`\`jsx
  import React, { Component } from 'react';
  import ReactDOM from 'react-dom';
  import { Toast } from 'react-stone';

  class App extends Component {
    openToast = () =>{
      Toast.top('--', 2000);
    }
    openMiddle = () =>{
      Toast.middle('--',2000)
    }
    openBottom = () =>{
      Toast.bottom('--',2000)
    }
    openErrorTopico = () =>{
      Toast.errorTopico('--',2000)
    }
    openYesTopico = () =>{
      Toast.yesTopico('--',2000)
    }
    render(){
      return (
        <div>
          <Button onClick={this.openToast} >top</Button>
          <Button onClick={this.openMiddle} >middle</Button>
          <Button onClick={this.openBottom} >bottom</Button>
          <Button onClick={this.openErrorTopico} >errorTopico</Button>
          <Button onClick={this.openYesTopico} >yesTopico</Button>
        </div>
      )
    }
  }

  ReactDOM.render(<App />, document.getElementById('root'));
  \`\`\`
`;

const api = `
  ## 

  - Toast.top('--', );
  - Toast.middle('--', );
  - Toast.bottom('--', );
  - Toast.errorTopico('--', );
  - Toast.yesTopico('--', );
`;

export {
  desc,
  base,
  api,
};



