import React, { Component } from 'react';
import { withRouter } from 'react-router';
import ReactMarkdown from 'react-markdown';
import CascaderSelector from '../../../components/CascaderSelector/CascaderSelector.jsx';

import * as code from './docs.js';
import DemoWrap from '../../components/DemoWrap/DemoWrap';

class CascaderSelectorPage extends Component {
  constructor(props) {
    super(props);
    this.state={
      dataSource:[
        {
          value: 'television',
          label: '',
          children: [{
            value: 'curveScreen',
            label: '',
            children: [{
              value: 'smart',
              label: '',
            },
            {
              value: 'normal',
              label: '',
            }],
          }],
        }, {
          value: 'fridge',
          label: ' ',
          children: [{
            value: 'doors',
            label: '',
            children: [{
              value: 'three',
              label: '',
            },
            {
              value: 'oppose',
              label: '',
            },
            {
              value: 'two',
              label: ' ',
            },
            {
              value: 'single',
              label: ' ',
            }],
          },
          {
            value: 'smartFridge',
            label: '',
            children: [{
              value: 'voice',
              label: ' ',
            },
            {
              value: 'screenble',
              label: '',
            },
            {
              value: 'touchSrceen',
              label: '',
            },
            {
              value: 'light',
              label: '',
            }],
          }],
        }
      ],
      initialValue:['television','curveScreen','smart']
    }
    this.cascaderSelect = this.cascaderSelect.bind(this);
  }
  componentDidMount(){
    console.log(this.props)
  }
  cascaderSelect(data){
    console.log('CascaderSelector value:--->' ,data);
  }


  render() {
    return (
      <div className="markdown">
        <ReactMarkdown source={code.desc} />
        <h2></h2>
        <DemoWrap desc="" code={code.base}>
          <CascaderSelector
            optionData={this.state.dataSource}
            onChange={this.cascaderSelect}
          />
          <br/>
        </DemoWrap>
        <DemoWrap desc="" code={code.initialValue}>
          <CascaderSelector
            optionData={this.state.dataSource}
            onChange={this.cascaderSelect}
            initialValue = {this.state.initialValue}
          />
          <br/>
        </DemoWrap>
       <ReactMarkdown source={code.api} />
      </div>
    );
  }
}

export default withRouter(CascaderSelectorPage);



